import React from "react";

const TripDetailsTabs = ({ trip, activeTab, setActiveTab }) => {
  const generateAvatarText = (name) => {
    if (!name) return "?";
    const nameParts = name.split(" ");
    if (nameParts.length === 1) {
      return nameParts[0][0].toUpperCase();
    }
    return (
      nameParts[0][0].toUpperCase() + nameParts[nameParts.length - 1][0].toUpperCase()
    );
  };
  const renderTabContent = () => {
    switch (activeTab) {
      case "discounts":
        return (
          <div className="p-4">
            <h3 className="font-semibold text-xl mb-4 text-blue-600">Ưu đãi đặc biệt</h3>
            <ul className="list-disc pl-6">
              <li className="mb-2">Giảm 50% tối đa 250k cho vé đầu tiên</li>
              <li>Giảm 20% cho mọi vé tiếp theo</li>
            </ul>
          </div>
        );
      case "policy":
        return (
          <div className="p-4">
            <h3 className="font-semibold text-xl mb-4 text-blue-600">Chính sách</h3>
            <ul className="list-disc pl-6">
              <li className="mb-2">Hoàn tiền 100% nếu huỷ trước 24 giờ.</li>
              <li className="mb-2">Hỗ trợ đổi vé miễn phí 1 lần.</li>
              <li>Bảo hiểm chuyến đi tối đa 1 tỷ VNĐ.</li>
            </ul>
          </div>
        );
      case "images":
        return (
          <div className="grid grid-cols-2 gap-4 p-4">
            {trip.busType.images &&
              trip.busType.images.map((image, index) => (
                <div key={index} className="relative">
                  <img
                    src={image}
                    alt={`Bus image ${index + 1}`}
                    className="w-full h-40 object-cover rounded-lg shadow-md"
                  />
                  <span className="absolute top-2 right-2 bg-blue-600 text-white text-sm px-2 py-1 rounded-lg">
                    {`#${index + 1}`}
                  </span>
                </div>
              ))}
          </div>
        );
        case "feedbacks":
          return (
            <div className="p-4">
              <h3 className="font-semibold text-xl mb-4 text-blue-600">Đánh giá khách hàng</h3>
              {trip.companyId.feedbacks && trip.companyId.feedbacks.length > 0 ? (
                <div className="space-y-6">
                  {trip.companyId.feedbacks.map((feedback, index) => (
                    <div
                      key={index}
                      className="p-4 border rounded-lg shadow-md bg-gray-50 hover:shadow-lg transition duration-200 flex items-start"
                    >
                      {/* Avatar */}
                      <div className="w-12 h-12 rounded-full bg-blue-600 text-white flex items-center justify-center font-bold text-lg mr-4">
                        {generateAvatarText(feedback.fullName)}
                      </div>
                      {/* Nội dung đánh giá */}
                      <div>
                        <div className="flex items-center justify-between mb-2">
                          <p className="font-bold text-gray-800">{feedback.fullName}</p>
                          <span className="text-yellow-500 text-lg">
                            {"★".repeat(feedback.rating)}
                            {"☆".repeat(5 - feedback.rating)}
                          </span>
                        </div>
                        <p className="text-gray-700 mb-2">{feedback.comment}</p>
                        <p className="text-gray-500 text-sm">
                          {new Date(feedback.createdAt).toLocaleDateString()}
                        </p>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <p>Chưa có đánh giá nào.</p>
              )}
            </div>
          );
        default:
          return null;
      }
    };
  return (
    <div>
      <div className="flex justify-around border-b mb-6">
        {["discounts", "policy", "images", "feedbacks"].map((tab, index) => (
          <button
            key={index}
            className={`px-6 py-3 text-sm font-semibold uppercase tracking-wide ${
              activeTab === tab
                ? "border-b-4 border-blue-600 text-blue-600"
                : "text-gray-500 hover:text-blue-600"
            }`}
            onClick={() => setActiveTab(tab)}
          >
            {tab === "discounts"
              ? "Ưu đãi"
              : tab === "policy"
              ? "Chính sách"
              : tab === "images"
              ? "Hình ảnh"
              : "Đánh giá"}
          </button>
        ))}
      </div>
      <div className="bg-white shadow-lg rounded-lg">{renderTabContent()}</div>
    </div>
  );
};

export default TripDetailsTabs;
